.container{
    display: flex;
    overflow: scroll;
    height:100vh;
    color:white;
    font-family:Georgia, 'Times New Roman', Times, serif;
}

.title{
    color: white;
    background-color: #185349;
    font-family:sans-serif;
    padding: 20px 0px;
    margin-bottom: 20px;
}

h1{
    margin: 0px;
}

.sidebar{
    background-color:#ecf4f3;
    flex: 20%;
    text-align:center;
}

.image-container{
    flex: 80%;
    color:black;
    font-size: 3rem;
    text-align: center;
}

.image-content{
    display: block;
    max-height: 95vh;
    height: auto;
    margin: 2.5vh;
    margin-left: auto;
    margin-right: auto;
    box-shadow:10px 10px 5px lightgray;
    
}

.photographer:hover{
    cursor: pointer;
}
