.photographer-container{
    margin-top: 100px;
    padding: 20px 0px;
    font-size: 1.5rem;
}

.photographer-profile-image{
    border: #57a99a solid 2px;
    border-radius: 50%;
}
.photographer-name, .photographer-username{
    margin: 10px;
    background-color:#57a99a;
}
.photographer-name:hover, .photographer-username:hover{
    color: black;
    background-color: #76dbd1;
    transition: 0.4s;
}

a:link, a:visited{
    color: white;
    text-decoration: none;
}